<template>
  <div class="area">
    <div v-for="(line, index) in area" :key="index">
      <span v-for="(item, index) in line" :key="index">
        <span :class="handleItemClass(item)">.....</span>
      </span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    area: {
      type: Array,
      default: () => {
        return [[]];
      },
    },
  },
  methods: {
    handleItemClass(item) {
      var style = item;
      return style;
    },
  },
};
</script>

<style lang="scss">

@mixin area-item($color) {
    color: transparent;
    background: $color;
    box-shadow: 0 0 5px $color;
  }

$ava-color: #9ac8e2;
$bella-color: #bd7d74;
$carol-color: #b8a6d9;
$diana-color: #e799b0;
$eileen-color: #576690;
$empty-color: #000000;

.area {
  .a {
    @include area-item($ava-color);
  }
  .b {
    @include area-item($bella-color);
  }

  .c {
    @include area-item($carol-color);
  }

  .d {
    @include area-item($diana-color);
  }

  .e {
    @include area-item($eileen-color);
  }

  .empty {
    @include area-item($empty-color);
  }
}
</style>
