<template>
  <div class="things">
    {{things.thingsName}}
  </div>
</template>

<script>
export default {
  props: {
    thingsType: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      things: {
        thingsName: ""
      },
    }
  },
  created() {
    if(this.thingsType === "b") {
          this.things.thingsName = "a box";
        }
  }
}
</script>

<style lang="scss" scoped>
.things {
  color: white;
}
</style>
