<template>
  <div id="app">
    <main class="main">
      <Area :area="area" />
    </main>
    <footer class="footer">
      <Things :thingsType="thingsType" v-if="thingsType" />
    </footer>
  </div>
</template>

<script>
import Area from "./components/Area";
import Things from "./components/Things";
// import AREAS from "./common/areas";
export default {
  components: {
    Area,
    Things,
  },
  data() {
    return {
      // area: AREAS.SERCET_ROOM,
      position: {
        x: 3,
        y: 5,
        px: 3,
        py: 5,
      },
      area: [],
      thingsType: "",
      size: 50,
    };
  },
  created() {
    this.createMap();
    this.makeRandomPath("a");
    this.makeRandomPath("b");
    this.makeRandomPath("c");
    this.makeRandomPath("d");
    this.makeRandomPath("e");
    // document.onkeydown = this.controller;
    // this.area[this.position.x][this.position.y] = "M";
  },
  methods: {
    createMap() {
      const size = this.size;
      this.area = new Array(size);
      for (let i = 0; i < size; i++) {
        this.area[i] = new Array(size);
      }
      for (let i = 0; i < size; i++) {
        for (let j = 0; j < size; j++) {
          this.area[i][j] = "empty";
        }
      }
    },
    makeRandomPath(item) {
      const size = this.size;
      var x = Math.floor(Math.random() * size),
        y = Math.floor(Math.random() * size),
        random = 0;
      (this.position.x = x), (this.position.y = y);
      // var num = 0;
      setInterval(() => {
        // if (num > 1000) {
        //   clearInterval(timer);
        //   this.$message("done");
        // }
        random = Math.floor(Math.random() * 4);
        if (random === 0 && x < size - 1) {
          x++;
        } else if (random === 1 && y < size - 1) {
          y++;
        } else if (random === 2 && x > 0) {
          x--;
        } else if (random === 3 && y > 0) {
          y--;
        }
        this.area[x][y] = item;
        this.area = this.area.slice(0);
        num++;
      }, 100);
    },
    controller($event) {
      const code = $event.keyCode;
      if ([37, 38, 39, 40].indexOf(code) !== -1) {
        this.moveController(code);
      }
    },
    moveController(code) {
      const direction = {
        left: this.area[this.position.x][this.position.y - 1],
        up: this.area[this.position.x - 1][this.position.y],
        right: this.area[this.position.x][this.position.y + 1],
        down: this.area[this.position.x + 1][this.position.y],
      };

      switch (code) {
        case 37:
          if (direction.left === "e") {
            this.position.px = this.position.x;
            this.position.py = this.position.y;
            this.position.y--;
            direction.left = this.area[this.position.x][this.position.y - 1];
          }
          break;
        case 38:
          if (direction.up === "e") {
            this.position.px = this.position.x;
            this.position.py = this.position.y;
            this.position.x--;
            direction.up = this.area[this.position.x - 1][this.position.y];
          }
          break;
        case 39:
          if (direction.right === "e") {
            this.position.px = this.position.x;
            this.position.py = this.position.y;
            this.position.y++;
            direction.right = this.area[this.position.x][this.position.y + 1];
          }
          break;
        case 40:
          if (direction.down === "e") {
            this.position.px = this.position.x;
            this.position.py = this.position.y;
            this.position.x++;
            direction.down = this.area[this.position.x + 1][this.position.y];
          }
          break;
      }
      // found something
      const directionList = [
        this.area[this.position.x][this.position.y - 1],
        this.area[this.position.x][this.position.y + 1],
        this.area[this.position.x - 1][this.position.y],
        this.area[this.position.x + 1][this.position.y],
      ];
      const objectList = ["b", "d", "f", "g"];
      const findResult = objectList.some((item) => {
        if (directionList.indexOf(item) !== -1) {
          this.findThings(item);
          return true;
        } else {
          return false;
        }
      });
      console.log(findResult);
      if (!findResult) {
        this.thingsType = "";
      }

      const newArea = this.area.slice(0);
      newArea[this.position.px][this.position.py] = "e";
      newArea[this.position.x][this.position.y] = "M";
      console.log("x", this.position.x, "y", this.position.y);
      this.area = newArea;
    },
    findThings(item) {
      this.thingsType = item;
    },
  },
  // created() {
  //
  // },
};
</script>
